import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { ICourseDetailsService, GetCourseDetailsRequest, GetCourseDetailsResponse, PostCourseDetailsRequest, PostCourseDetailsResponse } from '../@intermediate/view-course-details';

@Injectable({
  providedIn: 'root'
})

export class CourseDetailsService implements ICourseDetailsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Booking/TeeTimes/CourseDetails';
  }

  public Get(request: GetCourseDetailsRequest): Observable<GetCourseDetailsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetCourseDetailsResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostCourseDetailsRequest): Observable<PostCourseDetailsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostCourseDetailsResponse>(apiUrl, request);
  }
}
