import { Component, EventEmitter, Inject, OnInit, Output, SkipSelf } from '@angular/core';
import { ErrorStateMatcher } from '@angular/material/core';
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { GetCourseDetailsRequest, GetCourseDetailsResponse, ICourseDetailsService, PostCourseDetailsRequestForm } from '../../@intermediate/view-course-details';

@Component({
  selector: 'tei-tee-times-shared-view-course-details',
  templateUrl: './view-course-details.component.html',
  styleUrls: ['./view-course-details.component.scss'],
  providers: [
    ngFormGroupControlValueAccessorProviders(ViewCourseDetailsComponent)
  ]
})
export class ViewCourseDetailsComponent extends AbstractFormGroupControlValueAccessor implements OnInit
{
  model: GetCourseDetailsResponse;
  form: PostCourseDetailsRequestForm;
  @Output() notify = new EventEmitter<any>();

  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject('ICourseDetailsService') private CourseDetailsService: ICourseDetailsService)
  {
    super(_errorStateMatcher);
  }

  ngOnInit()
  {
    this.form = new PostCourseDetailsRequestForm();
    const request = new GetCourseDetailsRequest();

    this.load(request);
  }

  private load(request: GetCourseDetailsRequest)
  {
    if (!this.model)
    {
      this.model = new GetCourseDetailsResponse();
    }

    this.CourseDetailsService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.writeValue(this.model);
    });
  }
}
